* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
    html {
        font-family: 'Rubik', sans-serif;
    }
}
@layer components {
    .title {
        padding: 15px;
        box-shadow: 8px 8px 0 #6D21B9;
        @apply bg-black rounded flex justify-center items-center;
    }
}